.v2ex-card {
  color: #666;
  font-size: 13px;
  position: fixed;
  display: flex;
  padding-top: 10px;
  width: 250px;
  height: 200px;
  left: 0;
  top: 0; }
  .v2ex-card *,
  .v2ex-card *::before,
  .v2ex-card *::after {
    box-sizing: border-box; }
  .v2ex-card .vc-inner {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: 1px solid #e1e4e8;
    border-radius: 5px;
    box-shadow: 0 8px 10px rgba(149, 157, 165, 0.2); }
    .v2ex-card .vc-inner:before {
      top: -16px;
      right: 118px;
      left: auto;
      position: absolute;
      display: inline-block;
      content: '';
      border: 8px solid transparent;
      border-bottom-color: rgba(27, 31, 35, 0.15); }
    .v2ex-card .vc-inner:after {
      top: -14px;
      right: 119px;
      left: auto;
      border: 7px solid transparent;
      border-bottom-color: #fff;
      position: absolute;
      display: inline-block;
      content: ''; }
  .v2ex-card.vc-hide {
    display: none; }
